.expense_main{
    height: 90px;
    margin: 50px 30px;
    position: relative;
    background-color: rgba(39, 60, 117,0.7);
    background-blend-mode: multiply;
    background-position: center;
    background-size: cover;
    box-shadow: 0 0 10px #ff6b6b;
    color: white;
    display: flex;
    flex-direction: row;
   justify-content: space-between;
}
.expense_common{
    margin: 10px;
    width: 29%;
    text-align: center;
    font-size: 1rem;
}
.expense_date{
    width: 19% !important;
}
.expense_common h2{
    width: 100%;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50% ,-50%);
    font-size: 1.1rem;
}
.expense_common h2 span{
    position: relative;
    top: 10px;
    color: rgb(230, 233, 52);
    font-size: 1.3rem;
}
.change_btn{
    border-radius: 20px;
    padding: 15px;
    color: tomato;
    font-weight: bold;
    transition: all 0.3s linear;
    cursor: pointer;
}
.change_btn:hover{
    color: black;
}

.close_test{
    position: absolute;
    right: 0;
    z-index: 10;
    cursor: pointer;
}
.fa-window-close{
    position: relative;
    display: block;
    background-color: rgb(233, 62, 19);
    width: 20px;
    height: 18px;
    font-size: 1.2rem;
    transition: all 0.2s linear;
}
.fa-window-close:hover{
    color: red;
    background: black;
}
.amount_add{
    background-color: rgb(71, 49, 218);
    padding:2px 20px;
    border-radius: 10px;
}





@media only screen and (max-width:650px){
    .expense_common{
        width: 30%;
    }
    .expense_common h2{
        font-size: 1rem;
    }
    .expense_common h2 span{
        font-size: 0.8rem;
    }
    .expense_main{
        margin: 10px 5px;
    }




}