
.main_container{
    width: 100%;
    height: 100%;
    background-color: transparent;
}
.total_box{
    width: 50%;
    padding: 20px 1px;
    text-align: center;
    background: #3c6382a2;
}

.total_expenses{
    margin-left: 20px;
    padding: 10px 20px;
    background-color: rgb(71, 49, 218);
    border-radius: 10px;
    color: rgba(255, 242, 0,1.0);
}

.fa-cart-arrow-down{
    padding-left: 50px;
    font-size: 3rem;
    float: left;
    color: rgba(61, 61, 61,1.0);
}
.fa-inr{
    padding-right: 10px;
}

@media only screen and (max-width:1197px){
    .total_box{
        width: 70%;
    }

}
@media only screen and (max-width:807px){
    .total_box{
        width: 95%;
    }
    .fa-cart-arrow-down{
        padding-left: 10px;
       
    }

}

@media only screen and (max-width:490px){
    .total_box{
        width: 99%;
        margin: 0 auto;
    }
    .total_expenses{
        font-size: 1.2rem;
    }
    .total_box h1{
        font-size: 1.3rem;
    }
    .fa-cart-arrow-down{
        padding-left: 5px;
        font-size: 1.8rem;
       
    }
}