.input_Main_cover{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 auto;
    padding: 25px;
}
.input_Main_cover label{
    font-size: 1.5rem;
    color: rgba(255, 204, 204,1.0);
    font-weight: 700;
    margin-bottom: 10px;
    padding-left: 15px;
    letter-spacing: 5px;

}
.input_com{
    margin: 15px auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
    width: 50%;    
}

.input_com input[ type="text"] ,
.input_com input[ type="number"] ,
.input_com input[ type="date"] {
    width: 80%;
    height: 50px;
    padding: 15px;
    font-size: 1.3rem;
    border: 1px solid rgba(192, 192, 192, 0.918);
    border-radius: 2px;
    color: #ec1b1b;
    background-color: #dff9fb;
}
.input_com input[ type="submit"] {
    width: 150px;
    height: 50px;
    margin-top: 20px;
    color:#2C3A47 ;
    background-color: #D6A2E8;
    border-radius: 8px;
    font-size: 15px;
    font-weight: bolder;
    transition-property: color, background-color,border;
    transition-duration: 0.3s;

   
}
.input_com input[ type="submit"]:hover{
    color: #D6A2E8;
    background-color: #2C3A47 ;
    border: 5px solid #D6A2E8;
}





@media only screen and (max-width:680px) {
    .input_com{
        width: 95%;
        padding: 5px;
    }
    .input_com input[ type="text"] ,
    .input_com input[ type="number"] ,
    .input_com input[ type="date"]{
        width: 98%;
        margin: 0 auto;
        
    }
    .input_Main_cover{
        padding: 10px;
    }

    .input_com input[ type="submit"] {
        width: 85%;
        margin: 0 auto;
    }





}